.analytics {
  padding: 1.5rem;
  background-color: #f8fafc;
  min-height: 100vh;
  position: relative;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.loading-state .loading-spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid #e2e8f0;
  border-top: 3px solid #0d9488;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
.loading-state p {
  color: #64748b;
  font-size: 1.125rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.error-state .error-title {
  color: #dc2626;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 1rem 0;
}
.error-state .btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
}
.error-state .btn.btn-primary {
  background-color: #0d9488;
  color: white;
  border: none;
}
.error-state .btn.btn-primary:hover {
  background-color: #0f766e;
}

.analytics-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  background: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.analytics-header .header-left .main-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.analytics-header .header-left .subtitle {
  color: #64748b;
  font-size: 1.125rem;
  margin: 0;
}
.analytics-header .header-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.analytics-header .header-right .period-dropdown {
  position: relative;
}
.analytics-header .header-right .period-dropdown .period-select {
  appearance: none;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  font-size: 0.875rem;
  color: #374151;
  cursor: pointer;
  min-width: 150px;
}
.analytics-header .header-right .period-dropdown .period-select:focus {
  outline: none;
  border-color: #0d9488;
  box-shadow: 0 0 0 3px rgba(13, 148, 136, 0.1);
}
.analytics-header .header-right .period-dropdown .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.analytics-header .header-right .refresh-btn, .analytics-header .header-right .export-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
  border: none;
  cursor: pointer;
}
.analytics-header .header-right .refresh-btn:hover, .analytics-header .header-right .export-btn:hover {
  transform: translateY(-1px);
}
.analytics-header .header-right .refresh-btn {
  background-color: #f3f4f6;
  color: #374151;
}
.analytics-header .header-right .refresh-btn:hover {
  background-color: #e5e7eb;
}
.analytics-header .header-right .export-btn {
  background-color: #0d9488;
  color: white;
}
.analytics-header .header-right .export-btn:hover {
  background-color: #0f766e;
}

.analytics-tabs {
  display: flex;
  background: white;
  border-radius: 0.75rem;
  padding: 0.25rem;
  margin-bottom: 2rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  overflow-x: auto;
}
.analytics-tabs .tab-button {
  flex: 1;
  min-width: 0;
  padding: 0.75rem 1rem;
  border: none;
  background: transparent;
  color: #64748b;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0.5rem;
  white-space: nowrap;
}
.analytics-tabs .tab-button:hover {
  color: #374151;
  background-color: #f8fafc;
}
.analytics-tabs .tab-button.active {
  background-color: #0d9488;
  color: white;
}
.analytics-tabs .tab-button .tab-text {
  font-size: 0.875rem;
}

.tab-content .tab-placeholder {
  background: white;
  padding: 3rem 2rem;
  border-radius: 0.75rem;
  text-align: center;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.tab-content .tab-placeholder h3 {
  color: #1e293b;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.tab-content .tab-placeholder p {
  color: #64748b;
  font-size: 1.125rem;
}

.analytics-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.analytics-cards .analytics-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
}
.analytics-cards .analytics-card .card-header {
  margin-bottom: 1.5rem;
}
.analytics-cards .analytics-card .card-header .card-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.analytics-cards .analytics-card .card-header .card-subtitle {
  font-size: 0.875rem;
  color: #64748b;
  font-style: italic;
}
.analytics-cards .analytics-card .card-metrics .metric-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid #f1f5f9;
}
.analytics-cards .analytics-card .card-metrics .metric-item:last-child {
  border-bottom: none;
}
.analytics-cards .analytics-card .card-metrics .metric-item .metric-label {
  color: #64748b;
  font-weight: 500;
}
.analytics-cards .analytics-card .card-metrics .metric-item .metric-value {
  font-weight: 600;
  color: #1e293b;
}
.analytics-cards .analytics-card .card-metrics .metric-item .metric-value.warning {
  color: #f59e0b;
}
.analytics-cards .analytics-card .card-metrics .metric-item .metric-value.danger {
  color: #dc2626;
}

.abc-table-section {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}
.abc-table-section .section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 1.5rem 0;
}
.abc-table-section .table-container {
  overflow-x: auto;
}
.abc-table-section .table-container .analytics-table {
  width: 100%;
  border-collapse: collapse;
}
.abc-table-section .table-container .analytics-table th, .abc-table-section .table-container .analytics-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
}
.abc-table-section .table-container .analytics-table th {
  background-color: #f8fafc;
  font-weight: 600;
  color: #374151;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.abc-table-section .table-container .analytics-table td {
  color: #1e293b;
  font-size: 0.875rem;
}
.abc-table-section .table-container .analytics-table .abc-badge {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.abc-table-section .table-container .table-footer {
  padding: 1rem 0;
  text-align: center;
  color: #64748b;
  font-size: 0.875rem;
}

.metric-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.metric-cards-row .metric-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.metric-cards-row .metric-card .card-content {
  flex: 1;
}
.metric-cards-row .metric-card .card-content .metric-value {
  font-size: 2rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 0.5rem;
}
.metric-cards-row .metric-card .card-content .metric-value.warning {
  color: #f59e0b;
}
.metric-cards-row .metric-card .card-content .metric-label {
  font-size: 0.875rem;
  color: #64748b;
  margin-bottom: 0.5rem;
  display: block;
}
.metric-cards-row .metric-card .card-content .metric-change {
  font-size: 0.75rem;
  color: #64748b;
}
.metric-cards-row .metric-card .card-content .metric-change.positive {
  color: #059669;
}
.metric-cards-row .metric-card .metric-icon {
  width: 3rem;
  height: 3rem;
  color: #0d9488;
  flex-shrink: 0;
}

.charts-section {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.charts-section .chart-panel {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.charts-section .chart-panel .panel-header {
  margin-bottom: 1.5rem;
}
.charts-section .chart-panel .panel-header .panel-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.charts-section .chart-panel .panel-header .panel-subtitle {
  color: #64748b;
  font-size: 0.875rem;
  margin: 0;
}
.charts-section .chart-panel .chart-container .chart-placeholder, .charts-section .chart-panel .chart-container .line-chart {
  height: 260px;
  background-color: #f8fafc;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #64748b;
  font-size: 0.875rem;
}
.charts-section .chart-panel .chart-container canvas {
  width: 100%;
  height: 100% !important;
  background-color: #f8fafc;
  border-radius: 0.5rem;
  display: block;
}
.charts-section .chart-panel .table-container {
  overflow: auto;
}
.charts-section .chart-panel .table-container .panel-table {
  width: 100%;
  border-collapse: collapse;
}
.charts-section .chart-panel .table-container .panel-table th, .charts-section .chart-panel .table-container .panel-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.875rem;
  color: #1e293b;
}
.charts-section .chart-panel .table-container .panel-table th {
  background-color: #f8fafc;
  font-weight: 600;
  color: #374151;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-size: 0.75rem;
}
.charts-section .chart-panel .abc-breakdown {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.charts-section .chart-panel .abc-breakdown .abc-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  border: 1px solid #e2e8f0;
  background-color: #f8fafc;
  border-radius: 0.5rem;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #1f2937;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .badge {
  padding: 0.25rem 0.6rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .badge.a {
  background: #dbeafe;
  color: #1e3a8a;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .badge.b {
  background: #d1fae5;
  color: #065f46;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .badge.c {
  background: #ffedd5;
  color: #92400e;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .items {
  font-weight: 600;
}
.charts-section .chart-panel .abc-breakdown .abc-row .left .sub {
  font-size: 0.75rem;
  color: #64748b;
}
.charts-section .chart-panel .abc-breakdown .abc-row .right {
  text-align: right;
}
.charts-section .chart-panel .abc-breakdown .abc-row .right .value {
  font-weight: 700;
  color: #1e293b;
}
.charts-section .chart-panel .abc-breakdown .abc-row .right .qty {
  font-size: 0.75rem;
  color: #64748b;
}

.voice-assistant {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  background: #0d9488;
  color: white;
  border: none;
  cursor: pointer;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.voice-assistant:hover {
  background: #0f766e;
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .analytics {
    padding: 1rem;
  }
  .analytics-header {
    flex-direction: column;
    gap: 1rem;
  }
  .analytics-header .header-right {
    width: 100%;
    justify-content: space-between;
  }
  .analytics-tabs .tab-button {
    padding: 0.5rem 0.75rem;
    font-size: 0.75rem;
  }
  .analytics-cards {
    grid-template-columns: 1fr;
  }
  .metric-cards-row {
    grid-template-columns: 1fr;
  }
  .charts-section {
    grid-template-columns: 1fr;
  }
  .voice-assistant {
    bottom: 1rem;
    right: 1rem;
    width: 3rem;
    height: 3rem;
  }
}
